




































































.login-content {
  width: 100%;
  height: 100%;
  display: table;
}

.login-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.login-center {
  width: 350px;
  margin: 0 auto;
  padding: 3rem 3rem;
  background: #fff;
  border-radius: .3rem;
  box-shadow: 0 0 20px #ddd;
}

.login-center .title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 3rem;
  color: #4a4a4a;
}



