































































































label.error {
  display: inline-block;
  text-align: center;
  color: red;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.qrwrap {
  position: relative;
}

.qrcode-mask .close {
  padding: 1rem;
  position: absolute;
  top: 0;
  right: 0;
  color: #000;
}

.qrcode-mask .close .iconfont {
  font-size: 1.2rem;
}

.btn.btn-success:disabled {
  background: #ddd;
  border: 1px solid #ddd;
}



