




















































































































































































.qrcode {
  border: 1px solid #ddd;
  display: block;
  margin: 20px auto;
  height: auto;
  width: 40%;
}

.qrcode img {
  width: 100%;
  height: auto;
}

.m-order-list {
  margin: 2rem 0;
  padding: 0 1rem;
  background: #fff;
  border-top: 1px solid #E4E4E4;
  border-bottom: 1px solid #E4E4E4;
  font-size: 1.4rem;
}

.m-order-list .title {
  display: inline-block;
  width: 100%;
  font-weight: bold;
}

.m-order-list .pure-g {
  border-bottom: 1px solid #ddd;
  padding: 1rem 0;
}

.m-order-list .pure-g:last-child {
  border-bottom: 0 none;
}

.m-order-list .content {
  display: inline-block;
  width: 100%;
  text-align: right;
  color: #616161;
}

.m-order-list a {
  color: #25AD25;
}

.user-list a {
  font-size: 1.4rem;
  padding: 1rem;
  color: #25AD25;
  background: #fff;
  border-top: 1px solid #E4E4E4;
  border-bottom: 1px solid #E4E4E4;
  display: block;
  margin-bottom: -1px;
}

.user-list a .iconfont {
  float: right;
  color: #25AD25;
}

.ios-btn {
  display: block;
  -webkit-appearance: none;
  border-right: 0 none;
  border-left: 0 none;
  outline: none;
  width: 100%;
  padding: 1rem;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  background: #fff;
  font-size: 1.4rem;
  color: #25AD25;
  text-align: center;
}

.ios-btn.ios-btn-link {
  background: none;
  border: 0 none;
}

.ios-btn + .ios-btn {
  margin-top: -1px;
}

.ios-btn.ios-btn-danger {
  color: red;
  margin-top: 20px;
  text-decoration: underline;
}

.ios-btn:disabled {
  color: #bbb;
}

.center-box {
  display: table;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
}

.center-wrap {
  display: table-cell;
  vertical-align: middle;
}

.center-content {
  width: 80%;
  margin: 0 auto;
  background: #fff;
  padding: 1rem;
  border-radius: .3rem;
}

.center-content h1 {
  text-align: center;
  font-size: 1.6rem;
}

.center-content h1 span {
  float: right;
}

.center-content .body {
  padding: 2rem;
}

.center-content .body p {
  color: #bbb;
  font-size: 1.4rem;
}

.select.select-new {
  border: 1px solid #5DBB1A;
  border-radius: .3rem;
  margin-top: 1rem;
}



